<template>
	<div class="uploadContainer">
		<div>
			<h3>上传视频文件</h3>
			<el-upload
				style="width: 400px;"
				:data="form"
				:action="config.action"
				:name="config.name"
				:auto-upload="config.autoUpload"
				:file-list="config.fileList"
				ref="upload"
				:before-upload="beforeUpload"
				:on-preview="handlePreview"
				:on-remove="handleRemove"
				:on-success="handleSuccess"
				>
				<el-button slot="trigger" size="small" type="primary">选取文件</el-button>
				<el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
				
			</el-upload>
		</div>
		

		<!-- <div>
			<h3>上传视频封面</h3>

			<el-upload
				class="cover_upload"
				action="/api/post"
				:show-file-list="false"
				:on-success="handleAvatarScucess"
				:before-upload="beforeAvatarUpload">
				<img v-if="imageUrl" :src="imageUrl" class="avatar">
				<i v-else class="el-icon-plus avatar-uploader-icon"></i>
			</el-upload>
		</div> -->

		

		<div class="video_info">
			<h3>填写视频信息</h3>
			<el-form 
				ref="form"
				label-width="80px"
				label-position="top"
				style="margin:20px;width:60%;min-width:600px;"
				>

				<el-form-item label="视频标题">
					<el-input 
						:maxlength="20" 
						:minlength="3"
						size="large"
						placeholder="标题字数20字以内"
						v-model="title">
						
					</el-input>
				</el-form-item>

				<el-form-item label="视频简介">
					<el-input type="textarea" 
						:rows="8" 
						placeholder="视频详细信息"
						v-model="detail"></el-input>
				</el-form-item>

				<el-form-item label="视频分区">
				    <el-checkbox-group v-model="section">
				        <el-checkbox label="音乐区" value="music" name="section"></el-checkbox>
				        <el-checkbox label="番剧区" value="anime" name="section"></el-checkbox>
				        <el-checkbox label="电影区" value="movie" name="section"></el-checkbox>
				        <el-checkbox label="舞蹈区" value="dance" name="section"></el-checkbox>
				    </el-checkbox-group>
				</el-form-item>
				
				<el-form-item>
    				<el-button type="primary" @click="createCourse">提交视频信息</el-button>
				</el-form-item>
			</el-form>
		</div>
	</div>
</template>

<script>

	var fileList = [
			{
				name: 'food.jpeg',
				url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
			}, 
			{
				name: 'food2.jpeg', 
				url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}
		];

	var uploadConfig = {
		action: "http://up-z2.qiniu.com",
		headers: {
			"content-type": "multipart/form-data",
		},
		name: "file",
		autoUpload: false,
		fileList: []
	}

	export default {
		name: "upload_video",
		components: {

		},

		mounted: function () {
			// this.$http.get("/api/token")
			// 	.then(res => console.log("res:", res))
			// 	.catch(error => console.log("error:", error));
		},

		data (){
			return {
				config: uploadConfig,
				title: "",
				detail: "",
				section: "",
				videos:[],
				cover:"",
				form: {}, // 视频上传token等附加信息
				courseId: "",
				course: null,

			}
		},
		methods: {
			validate: function (){
				if (!this.title) {
					return "请填写标题"
				}
				else if (!this.detail){
					return "请填写详细信息"
				}
				else if (!section) {
					return "请选择分区"
				}
				else if (!videos.length){
					return "请上传至少一条视频"
				}
				else {
					return true;
				}
			},
			calcelTimeoutDelete: function () {
				this.$http.post("/api/cancel_timeout_delete",{
					videos: this.videos
				})
				.then(res => {
					console.log("calcel timeout task")
				})
				.catch(error => {
					console.log("got error in calcel timeout task:", error);
				})
			},
			deleteTimeout: function (data) {
				this.$http.post("/api/timeout_delete", data)
					.then(res => console.log("set timeout delete:", res))
					.catch(error => console.log("got error in set timeout delete:", error))
			},
			handlePreview: function (file){
				console.log("file:", file);
				
			},
			beforeUpload: function (file) {
				var self = this,
					fileName = file.name;

				console.log("file name:", fileName);

				return self.$http.post("/api/token", {
					key: fileName
				})
				.then(res => {
					self.form = {
						key: res.data.key,
						token: res.data.token
					}

					self.$nextTick(function (){
						
					})
				})
				.catch(error => console.log("upload error:", error));
					
			},
			handleRemove: function (file, fileList) {
				var res = file.response;
				this.$http.post("/api/delete", {
					type: "video",
					key: res.key
				})
				.then(res => {
					console.log("delete response:", res);
					if (res.data.code == 1){

						this.videos = this.videos.filter(function(video) {
							return (video.key !== res.key)
						});

						alert("delete success");
					}
				})
				.catch(error => console.log("got a error in delete:", error));
			},
			handleSuccess: function (res, file, fileList){
				console.log("success");
				this.videos.push(res);
				console.log("server response:", res);
				// console.log("file: ", file.name);
				this.deleteTimeout(res);
				console.log("fileList", fileList.length);
			},
			submitUpload() {
				console.log("form:", this.form);
				this.$refs.upload.submit();	
		    },
		    createCourse: function () {
		    	var self = this;
		    	var validated = this.validate();

		    	if (validated !== true){
		    		alert("validated")
		    		return;
		    	}
		    	var course = {
		    		title: this.title,
		    		detail: this.detail,
		    		section: this.section,
		    		videos: this.videos,
		    		cover: this.cover,
		    	}
		    	if (this.course){
		    		course = Object.assgn(this.course, course);
		    	}
		    	course.comments = course.comments || [];
		    	course.views = course.views || 0;

		    	console.log("course:", course);
		    	return;

		    	this.$http.post("/api/new_course", {
		    		course: course
		    	})
		    	.then(res => {
		    		console.log("create new course:", res);
		    		this.calcelTimeoutDelete();
		    	})
		    	.catch(error => {
		    		console.log("new course error:", error)
		    	});
		    }
		}
	}
</script>

<style>
.cover_upload .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}

.avatar-uploader .el-upload:hover {
    border-color: #20a0ff;
}

.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
}

.avatar {
    width: 178px;
    height: 178px;
    display: block;
}
</style>


<style scoped>
.uploadContainer {
    margin-bottom: 50px;
}


</style>



